




















































import common from '../../common/common.js';
import {
	mapState
} from 'vuex';
export default {
	data() {
		return {
			userinfo: [],
			array: ['男', '女'],
			textValue: 0,
			array2: ['消防一队队长', '消防2队队长', '消防3队队长'],
			textValue2: 0
		}
	},
	computed: {
		...mapState(['user']),
	},
	methods: {
		islogin: function() {
			if (this.user.user === null) {
				uni.redirectTo({
					url: '../../pages/manage/login'
				})
				return;
			}
		},
		textChange: function(evt) {

			this.textValue = evt.detail.value;
		},
		textChange2: function(evt) {
			this.textValue2 = evt.detail.value;
		},
		getuser: function() {
			if (this.user.user === null) {
				this.userinfo = null
				return false
			}

			let that = this;
			uni.request({
				url: common.hosturl + "user/getuserinfo.html",
				method: "POST",
				data: {
					token: this.user.user.token,
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {

					//console.log(JSON.stringify(res.data))
					if (res.data.code == 0) {

						that.userinfo = res.data.userinfo;

					} else {
						that.userinfo = null;
						return false
					}
				},
				fail: function() {
					uni.showToast({
						icon: 'none',
						title: '网络错误'
					});
				}
			})

		}
	},
	onLoad: function() {
		this.islogin();
	},
	onShow: function() {
		this.getuser();
	}
}



